import React from "react";

interface ChatMessageProps {
  message: string;
  fromMe: boolean;
  timestamp: Date;
  author?: string;
}

const ChatMessage = ({ message, fromMe, timestamp, author }: ChatMessageProps) => {
  const messageClasses = fromMe
    ? "bg-primary text-primary-foreground ml-auto"
    : "bg-muted text-muted-foreground mr-auto";
  
  return (
    <div className={`max-w-xs sm:max-w-md p-3 rounded-lg shadow-sm ${messageClasses}`}>
      {!fromMe && author && (
        <p className="text-xs font-semibold mb-1 opacity-70">{author}</p>
      )}
      <p className="text-sm leading-relaxed">{message}</p>
      <span className="text-xs opacity-60 block mt-1">
        {timestamp.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
      </span>
    </div>
  );
};

export default ChatMessage;
