"use client";

import { useEffect, useState, useRef } from "react";
import { useSearchParams } from "next/navigation";
import ChatMessage from "@/components/chat/ChatMessage";
import ChatInput from "@/components/chat/ChatInput";
import { connectWebSocket, sendWebSocketMessage, closeWebSocket } from "@/lib/websocket";

interface Message {
  id: string;
  text: string;
  fromMe: boolean;
  timestamp: Date;
  author?: string;
}

export default function ChatPage() {
  const [messages, setMessages] = useState<Message[]>([
    {
      id: "1",
      text: "Hello! Welcome to the chat.",
      fromMe: false,
      timestamp: new Date(Date.now() - 60000),
      author: "Alice"
    },
    {
      id: "2", 
      text: "Hi there! Thanks for the welcome.",
      fromMe: true,
      timestamp: new Date(Date.now() - 30000)
    }
  ]);
  
  const [wsError, setWsError] = useState("");
  const [isConnected, setIsConnected] = useState(false);
  const wsRef = useRef<WebSocket | null>(null);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const searchParams = useSearchParams();
  const contactId = searchParams.get("id");
  const contactName = contactId ? `Contact ${contactId}` : "General Chat";

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  useEffect(() => {
    // Initialize WebSocket connection
    wsRef.current = connectWebSocket({
      onOpen: () => {
        setIsConnected(true);
        setWsError("");
      },
      onMessage: (msg: string) => {
        // Create a new message from WebSocket
        const newMessage: Message = {
          id: Date.now().toString(),
          text: msg,
          fromMe: false,
          timestamp: new Date(),
          author: "Echo Server"
        };
        setMessages((prev) => [...prev, newMessage]);
      },
      onError: (err) => {
        setWsError("Connection error. Messages may not be delivered in real-time.");
        setIsConnected(false);
      },
      onClose: () => {
        setWsError("Disconnected. Attempting to reconnect...");
        setIsConnected(false);
      },
    });

    return () => {
      closeWebSocket(wsRef.current);
    };
  }, []);

  const handleSend = (messageText: string) => {
    if (messageText.trim() === "") return;
    
    // Add the outgoing message immediately for instant feedback
    const newMessage: Message = {
      id: Date.now().toString(),
      text: messageText,
      fromMe: true,
      timestamp: new Date()
    };
    
    setMessages((prev) => [...prev, newMessage]);
    
    // Try to send via WebSocket
    const sent = sendWebSocketMessage(wsRef.current, messageText);
    if (!sent) {
      setWsError("Failed to send message. Please check your connection.");
    }
  };

  return (
    <div className="flex flex-col h-full max-h-[calc(100vh-8rem)]">
      {/* Chat Header */}
      <header className="p-4 border-b border-border bg-card">
        <div className="flex items-center justify-between">
          <div>
            <h2 className="text-lg font-semibold">{contactName}</h2>
            <p className="text-sm text-muted-foreground">
              {isConnected ? "Online" : "Connecting..."}
            </p>
          </div>
          {wsError && (
            <div className="text-xs text-destructive bg-destructive/10 px-2 py-1 rounded">
              {wsError}
            </div>
          )}
        </div>
      </header>

      {/* Messages Area */}
      <div className="flex-1 overflow-y-auto p-4 space-y-4 bg-muted/20">
        {messages.map((msg) => (
          <div key={msg.id} className={`flex ${msg.fromMe ? 'justify-end' : 'justify-start'}`}>
            <ChatMessage 
              message={msg.text} 
              fromMe={msg.fromMe} 
              timestamp={msg.timestamp}
              author={msg.author}
            />
          </div>
        ))}
        <div ref={messagesEndRef} />
      </div>

      {/* Chat Input */}
      <footer className="p-4 border-t border-border bg-card">
        <ChatInput onSend={handleSend} disabled={!isConnected && !wsError} />
      </footer>
    </div>
  );
}
