interface WebSocketOptions {
  onMessage: (msg: string) => void;
  onError: (error: any) => void;
  onClose: () => void;
  onOpen?: () => void;
}

export function connectWebSocket(options: WebSocketOptions): WebSocket | null {
  try {
    // For demo purposes, we'll simulate WebSocket with a mock connection
    // In a real app, this would be something like: ws://localhost:8080 or wss://your-server.com
    const ws = new WebSocket("wss://echo.websocket.org/");
    
    ws.onopen = () => {
      console.log("WebSocket connected");
      options.onOpen?.();
    };
    
    ws.onmessage = (event) => {
      try {
        // Handle incoming messages
        options.onMessage(event.data);
      } catch (error) {
        console.error("Error processing WebSocket message:", error);
        options.onError(error);
      }
    };
    
    ws.onerror = (error) => {
      console.error("WebSocket error:", error);
      options.onError(error);
    };
    
    ws.onclose = (event) => {
      console.warn("WebSocket closed:", event.code, event.reason);
      options.onClose();
      
      // Auto-reconnect after 3 seconds
      setTimeout(() => {
        console.log("Attempting to reconnect...");
        connectWebSocket(options);
      }, 3000);
    };
    
    return ws;
  } catch (error) {
    console.error("Failed to create WebSocket connection:", error);
    options.onError(error);
    return null;
  }
}

export function sendWebSocketMessage(ws: WebSocket | null, message: string): boolean {
  if (!ws) {
    console.error("WebSocket is not initialized");
    return false;
  }
  
  if (ws.readyState === WebSocket.OPEN) {
    try {
      ws.send(message);
      return true;
    } catch (error) {
      console.error("Error sending WebSocket message:", error);
      return false;
    }
  } else {
    console.error("Cannot send message, WebSocket is not open. State:", ws.readyState);
    return false;
  }
}

export function closeWebSocket(ws: WebSocket | null): void {
  if (ws && ws.readyState === WebSocket.OPEN) {
    ws.close();
  }
}
